import React, { Component }  from 'react'
import { Link } from 'react-router-dom'
import {
  CButton,
 
  CContainer,

  CRow
} from '@coreui/react'
import { Button, Card,CardBody,Alert, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row, CardHeader } from 'reactstrap';
import CIcon from '@coreui/icons-react'
import logo from '../../../assets/ma.png'
import $ from 'jquery'
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
import conf from '../../../conf.js'

class ForgetPwd extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      ok:"",
      soumis:false,
      valider:true,
      error:false

    }
  }

  componentDidMount() {
    
  }

  
  handleReinitialiseMotDePasse=()=>{


    let  token=  JSON.parse(localStorage.getItem('token_auth'))
  

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      ///'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify({
        "email": $("#email").val(),
        "username":"",
        "password":"",
        // "username":$("#email").val(),
      }
  )
  };

  fetch(conf.url+"user/forgot",requestOptions)
  .then((response) => response.json())
  .then((data) => {
     console.log("okkk====>",data)
     if(data!==0){
      this.setState({valider:false,soumis:true,error:false})
     }else{
      this.setState({valider:true,soumis:false,error:true})
     }
     
     

   }
   
  );
    
  } 
  handleRetourAcccueil=()=>{
    this.setState({ok:<Redirect to='/accueil-visiteur'/>})
  }

  render (){

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      {this.state.ok}
      <CContainer>
      <button onClick={this.handleRetourAcccueil} className="get-started-btn4 text-white mb-5" > <i class='bx bxs-home-smile' ></i>  Retour</button>
        <CRow className="justify-content-center">
          <Col md="4">
                <CardGroup>
  
                  <Card className="p-4 bg-white " border-radius="2px">
                    {/* <h3 ClassName="mb-3">Mot de passe oublié</h3> */}
                    <h3 className="p-4" >Mot de passe oublié</h3>
                    <CardBody>
                        
                      <Form ClassName="mt-3">
                        {
                          this.state.valider?
                          <Row>
                          <InputGroup className="mb-3">
                            <Input type="email" id="email" placeholder="Email" autoComplete="Email" />
                          </InputGroup>
                          
                          <Col>
                            {/* <Button onClick={this.handleReinitialiseMotDePasse} color="success" className="px-4 text-white validation btn-block">Réinitialiser mot de passe</Button> */}
                            <a 
                              onClick={this.handleReinitialiseMotDePasse}
                              class="get-started-btn4 text-white text-center a btn-block">Se connecter
                            </a>
                          </Col>
                        </Row>:
                        <>
                        {
                          this.state.error?
                        <Row>
                          <Alert color="danger">
                              Email incorrect!!.
                          </Alert>
                        </Row>:null
                       
                        }
                      
                        </>
                        }
                        { this.state.soumis?
                           <Row>
                           <Alert color="success">
                               Veuillez vérifier votre boite email.
                           </Alert>
                           </Row>:null
                        }
                        
                        <hr></hr>

                        <Row>
                          <Col>
                            {/* <Button color="info" href="/#/login" className="px-4 text-white validation btn-block">Se connecter</Button> */}
                            <a 
                              href="/#/login" 
                              class="get-started-btn5 text-white text-center a btn-block">Se connecter
                            </a>
                          </Col>
                        </Row>

                        <hr></hr>

                        <Row>
                          <Col>
                            {/* <Button color="info" href="/#/register" className="px-4 text-white validation btn-block">S'inscrire</Button> */}
                            <a 
                              href="/#/register" 
                              class="get-started-btn5 text-white text-center a btn-block">S'inscrire
                            </a>
                          </Col>
                        </Row>

                        

                        <Row>
                          <label id="msg"></label>
                        </Row>

                      </Form>
                    </CardBody>
                  </Card>
                  
                </CardGroup>
              </Col>
        </CRow>
      </CContainer>
    </div>
  )
}
}

export default ForgetPwd
